export function WindowsPopup({
  id,
  message,
  type = "UPDATE",
  x,
  y,
  onClose,
  onInteract,
  zIndex = 100,
  isGlitching = false,
}) {
  const [isDragging, setIsDragging] = useState(false);
  const [position, setPosition] = useState({ x, y });
  const [isHanging, setIsHanging] = useState(false);
  const popupInfo = POPUP_TYPES[type] || POPUP_TYPES.UPDATE;
  const messageData =
    typeof message === "object"
      ? message
      : { message, buttons: ["OK", "Annuler"] };

  // Simulation de freeze al√©atoire
  useEffect(() => {
    if (Math.random() > 0.7) {
      const hangTimeout = setTimeout(() => {
        setIsHanging(true);
        setTimeout(() => setIsHanging(false), 1500 + Math.random() * 2000);
      }, 1000);
      return () => clearTimeout(hangTimeout);
    }
  }, []);

  const handleButtonClick = (e) => {
    e.stopPropagation();
    if (isHanging) return;
    onInteract?.();
  };

  const handleClose = (e) => {
    e.stopPropagation();
    if (isHanging) return;
    onClose?.(id);
  };

  return (
    <div
      className={`absolute bg-white rounded-lg shadow-2xl overflow-hidden animate-popIn select-none
        ${isHanging ? "opacity-50 cursor-wait" : ""}
        ${isGlitching ? "animate-glitch" : ""}
        ${popupInfo.urgent ? "ring-2 ring-red-500" : ""}
      `}
      style={{
        left: `${position.x}%`,
        top: `${position.y}%`,
        minWidth: "320px",
        maxWidth: "400px",
        zIndex: zIndex,
      }}
    >
      {/* Barre de titre */}
      <div
        className="px-3 py-2 flex items-center justify-between cursor-move"
        style={{ backgroundColor: popupInfo.color }}
        onMouseDown={() => setIsDragging(true)}
      >
        <div className="flex items-center gap-2">
          <span className="text-lg">{popupInfo.icon}</span>
          <span className="text-white text-sm font-medium">
            {popupInfo.title}
          </span>
        </div>
        <div className="flex items-center gap-1">
          <button
            onClick={handleClose}
            className="text-white hover:bg-white/20 w-7 h-7 flex items-center justify-center rounded transition-colors"
            disabled={isHanging}
          >
            ‚Äî
          </button>
          <button
            onClick={handleClose}
            className="text-white hover:bg-red-500 w-7 h-7 flex items-center justify-center rounded transition-colors"
            disabled={isHanging}
          >
            ‚úï
          </button>
        </div>
      </div>

      {/* Contenu */}
      <div className="p-4 text-gray-800">
        <p className="text-sm leading-relaxed">{messageData.message}</p>

        {/* Boutons */}
        <div className="mt-4 flex gap-2 justify-end flex-wrap">
          {messageData.buttons.map((btn, i) => (
            <button
              key={i}
              onClick={handleButtonClick}
              className={`px-4 py-1.5 rounded text-sm font-medium transition-colors
                ${
                  i === 0
                    ? "bg-[#0078d4] text-white hover:bg-[#106ebe]"
                    : "bg-gray-100 text-gray-700 hover:bg-gray-200 border border-gray-300"
                }
                ${isHanging ? "cursor-wait opacity-50" : ""}
              `}
              disabled={isHanging}
            >
              {btn}
            </button>
          ))}
        </div>
      </div>

      {/* Indicateur de "ne r√©pond pas" */}
      {isHanging && (
        <div className="absolute inset-0 bg-white/80 flex items-center justify-center">
          <div className="text-center">
            <div className="w-8 h-8 border-4 border-blue-500 border-t-transparent rounded-full animate-spin mx-auto mb-2" />
            <p className="text-xs text-gray-500">Ne r√©pond pas...</p>
          </div>
        </div>
      )}
    </div>
  );
}

export function usePopupManager(isActive, onTriggerCrash, playSound) {
  const [popups, setPopups] = useState([]);
  const [interactionCount, setInteractionCount] = useState(0);
  const CRASH_THRESHOLD = 7; // Nombre d'interactions avant crash possible

  // Stocker la ref pour √©viter les re-renders
  const crashRef = useRef(onTriggerCrash);
  crashRef.current = onTriggerCrash;

  // Timer de 2min max avant crash automatique (si pas de clic)
  useEffect(() => {
    if (!isActive) return;

    const maxTimeBeforeCrash = setTimeout(() => {
      crashRef.current?.();
    }, 90000); // 120 secondes = 2min

    return () => clearTimeout(maxTimeBeforeCrash);
  }, [isActive]);

  // G√©n√©rer des popups p√©riodiquement - PLUS FR√âQUENT
  useEffect(() => {
    if (!isActive) return;

    const spawnPopup = () => {
      const messageData =
        POPUP_MESSAGES[Math.floor(Math.random() * POPUP_MESSAGES.length)];
      const newPopup = {
        id: Date.now() + Math.random(),
        type: messageData.type,
        message: messageData,
        x: Math.random() * 60 + 5,
        y: Math.random() * 60 + 5,
        zIndex: 100 + popups.length,
      };

      setPopups((prev) => [...prev, newPopup]);
      playSound?.();
    };

    // Premier popup apr√®s 2 secondes
    const initialTimeout = setTimeout(spawnPopup, 2000);

    // Deuxi√®me popup apr√®s 4 secondes
    const secondTimeout = setTimeout(spawnPopup, 4000);

    // Troisi√®me popup apr√®s 6 secondes
    const thirdTimeout = setTimeout(spawnPopup, 6000);

    // Puis toutes les 3-5 secondes (plus de popups!)
    const interval = setInterval(() => {
      spawnPopup();
      // 30% de chance de spawn 2 popups d'un coup
      if (Math.random() > 0.7) {
        setTimeout(spawnPopup, 500);
      }
    }, 3000 + Math.random() * 2000);

    return () => {
      clearTimeout(initialTimeout);
      clearTimeout(secondTimeout);
      clearTimeout(thirdTimeout);
      clearInterval(interval);
    };
  }, [isActive, playSound]);

  const closePopup = useCallback((id) => {
    setInteractionCount((prev) => {
      const newCount = prev + 1;
      // 40% de chance de crash apr√®s le seuil
      if (newCount >= CRASH_THRESHOLD && Math.random() > 0.6) {
        crashRef.current?.();
      }
      return newCount;
    });
    setPopups((prev) => prev.filter((p) => p.id !== id));
  }, []);

  const handleInteract = useCallback(() => {
    setInteractionCount((prev) => {
      const newCount = prev + 1;
      // Crash garanti si on d√©passe le seuil + 2
      if (newCount >= CRASH_THRESHOLD + 2) {
        crashRef.current?.();
      }
      return newCount;
    });
  }, []);

  return {
    popups,
    closePopup,
    handleInteract,
    interactionCount,
  };
}

export default WindowsPopup;
